.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  gap: 40px;

  @media (min-width: 900px) {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
  }
  &__main,
  &__sidebar {
    padding: 40px;
  }
  &__main {
    background-color: hsl(240, 99%, 33%);
  }
  &__sidebar {
    background-color: hsla(300, 40%, 52%, 0.678);
  }
}
